import axios from '@/libs/api.request'

/**
 *  
 * @param pageSize id
 * @param pageNumber id
 * @param name  
 * @returns {*}
 */
export const getErrList = ({ pageSize, pageNumber, name}) => {
  const data = {
    pageSize,
    pageNumber,
    name
  }
  return axios.request({
    url: 'malfunctionInfo/listMalfunctionInfo',
    params: data,
    method: 'get'
  })
}


export const errDetail = ({id}) => {
  return axios.request({
    url: `malfunctionInfo/${id}`,
    method: 'get'
  })
}


/**
 *  
 * @param typeId id
 * @param modelId id
 * @param malCode 
 * @param description 
 * @param malName 
 * @returns {*}
 */
export const getNewCode = (params) => {
  return axios.request({
    url: `malfunctionInfo/addMalfunctionInfo`,
    data: params,
    method: 'post'
  })
}

/**
 * 
 * @param id id
 * @param typeId id
 * @param modelId id
 * @param malCode 
 * @param description 
 * @param malName 
 * @returns {*}
 */
export const getEditCode = (params) => {
  return axios.request({
    url: `malfunctionInfo/editMalfunctionInfo`,
    data: params,
    method: 'post'
  })
}



    /**
 * 
 * @param id id
 * @returns {*}
 */
export const deleteCodeInfo = ({id}) => {
  const data = {
    id
  }
    return axios.request({
    url: 'malfunctionInfo/delMalfunctionInfo',
    data,
    method: 'post'
  })
}



